/* button css */
.btn {
  background: white;
  background-image: -webkit-linear-gradient(top, white, white);
  background-image: -moz-linear-gradient(top, white, white);
  background-image: -ms-linear-gradient(top, white, white);
  background-image: -o-linear-gradient(top, white, white);
  background-image: linear-gradient(to bottom, white, white);
  font-family: Georgia;
  color: blue;
  font-size: 20px;
  padding: 10px 40px 10px 40px;
  border: solid #1555d6 3px;
  text-decoration: none;
}

.btn:hover {
  background: #ade0ff;
  background-image: -webkit-linear-gradient(top, #ade0ff, #b2e0ff);
  background-image: -moz-linear-gradient(top, #ade0ff, #b2e0ff);
  background-image: -ms-linear-gradient(top, #ade0ff, #b2e0ff);
  background-image: -o-linear-gradient(top, #ade0ff, #b2e0ff);
  background-image: linear-gradient(to bottom, #ade0ff, #b2e0ff);
  text-decoration: none;
}
/* button css ends */

